import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
  // ,vuedata
  state: {
    cart_length: 0,  // 
  },

  // ,vuemethods
  mutations: {    // 
    add_cart (state, cart_length) {
      state.cart_length = cart_length;
    }
  }

})




